-----------------------------\n\n// header / footer\n@import './components/_header';\n\n// --------------------------------------------------------------------------\n\n@import './dev/vzmsk1.scss';\n@import './dev/markusDM.scss';\n@import './dev/ukik0.scss';\n","*,\n*::before,\n*::after {\n  box-sizing: border-box;\n}\nhtml {\n  font-family: 'EuclidCircularA'; // шрифт по умолчанию по сайту\n  font-size: 0.5208335vw; // на разрешении 1920 0.520835vw === 10px\n  font-style: normal;\n  font-weight: normal;\n  -webkit-animation: bugfix infinite 1s;\n  line-height: 1.2;\n  margin: 0;\n  padding: 0;\n}\n\nbody {\n  font-style: normal;\n  font-weight: normal;\n  -webkit-animation: bugfix infinite 1s;\n  line-height: 1.2;\n  margin: 0;\n  padding: 0;\n  color: #262525; // цвет по умолчанию текста по сайту\n  background-color: #ffffff;\n}\n\ninput,\ntextarea {\n  -webkit-animation: bugfix infinite 1s;\n  line-height: inherit;\n  margin: 0;\n  padding: 0;\n  background-color: transparent;\n  border: none;\n  color: inherit;\n}\na {\n  color: unset;\n}\na,\na:hover {\n  text-decoration: none;\n}\n\nbutton,\ninput,\na,\ntextarea {\n  outline: none;\n  cursor: pointer;\n  font: inherit;\n  &:focus {\n    outline: none;\n  }\n  &:active {\n    outline: none;\n  }\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  font: inherit;\n  margin: 0;\n  padding: 0;\n}\np {\n  margin-top: 0;\n  margin-bottom: 0;\n}\n\nimg {\n  width: 100%;\n  height: auto;\n  display: block;\n}\n\nbutton {\n  border: none;\n  color: inherit;\n  font: inherit;\n  text-align: inherit;\n  padding: 0;\n  background-color: transparent;\n}\nul {\n  padding: 0;\n  margin: 0;\n}\n\nul li {\n  margin: 0;\n  padding: 0;\n  list-style: none;\n}\n\n.container {\n  width: 141rem;\n  margin: 0 auto;\n}\n\ninput[type='number']::-webkit-inner-spin-button,\ninput[type='number']::-webkit-outer-spin-button {\n  -webkit-appearance: none;\n  margin: 0;\n}\n\ninput[type='number'] {\n  -moz-appearance: textfield;\n}\n\n@media (min-width: 1920px) {\n  html {\n    font-size: 10px;\n  }\n}\n\n@media (max-width: 48em) {\n  html {\n    font-size: 5px;\n    font-size: 1.5625vw;\n    font-size: calc((100 / 375) * 5vw); // где 375 это ширина моб версии макета\n    -webkit-text-size-adjust: none;\n  }\n\n  body {\n    -webkit-text-size-adjust: none;\n  }\n\n  .container {\n    padding: 0 4rem; // в моб версии отступ от края задаем для всех контейнеров, а там где не нужно можем точечно убрать\n    width: 100%;\n  }\n}\n",".main-collection {\n  background-color: #cad2f4;\n  padding: 16rem 0;\n  // .main-collection__inner\n  &__inner {\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    gap: 8rem;\n  }\n  // .main-collection__cards\n  &__cards.swiper {\n    position: relative;\n    width: 83.6rem;\n    height: 73.3rem;\n    margin: 0;\n  }\n  &__cards-item.swiper-slide {\n    width: 51.5rem;\n    height: 51.5rem;\n    border-radius: 4rem;\n    position: absolute;\n    padding: 1.2rem;\n    overflow: hidden;\n  }\n  // .main-collection__title\n  &__title {\n    color: #1f1f22;\n    font-family: 'DrukCyr';\n    font-size: 10rem;\n    font-style: normal;\n    font-weight: 500;\n    line-height: 110%; /* 110px */\n    text-transform: uppercase;\n    margin-bottom: 2rem;\n\n    width: 69.5rem;\n  }\n  // .main-collection__description\n  &__description {\n    color: #6a6a6a;\n    font-size: 2rem;\n    font-style: normal;\n    font-weight: 400;\n    line-height: 140%; /* 28px */\n    margin-bottom: 5rem;\n    width: 61.8rem;\n  }\n}\n.collection-item {\n  display: flex;\n  flex-direction: column;\n  justify-content: space-between;\n\n  &--rat {\n    top: 0;\n    left: 0;\n    z-index: 2;\n\n    background: linear-gradient(\n      223deg,\n      #8792ed 1.18%,\n      #5f6eda 34.96%,\n      #6778df 68.58%,\n      #8792ed 100%\n    );\n  }\n  &--snail {\n    right: 0;\n    bottom: 0;\n    z-index: 1;\n\n    background: linear-gradient(\n      226deg,\n      #455265 0%,\n      #11161e 40